import { FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Stack, Typography } from '@mui/material';
import { useContext, useMemo, useState } from 'react';
import { getCharacterByHighestSkillLevel } from '../../../../../parsers/misc';
import { AppContext } from '../../../../common/context/AppProvider';
import { CardTitleAndValue } from '../../../../common/styles';
import { notateNumber } from '../../../../../utility/helpers';
const CogStatCalculator = () => {
  const {
    state
  } = useContext(AppContext);
  const [cogType, setCogType] = useState(5);
  const highestDK = useMemo(() => getCharacterByHighestSkillLevel(state?.characters, '', 'construction'), [state?.characters]);
  const highestConstruction = highestDK?.skillsInfo?.construction?.level;
  const mainConstructionValue = Math.pow(highestConstruction / 3 + 0.7, 1.3 + 0.05 * cogType) / 4 + Math.pow(3, cogType - 2);
  const constructionMin = mainConstructionValue * .4;
  const constructionMax = mainConstructionValue * 3;
  const handleChange = event => {
    setCogType(event.target.value);
  };
  return <Stack alignItems={'center'} data-sentry-element="Stack" data-sentry-component="CogStatCalculator" data-sentry-source-file="CogStatCalculator.jsx">
      <CardTitleAndValue title={'Highest Cons level'} value={highestDK?.skillsInfo?.construction?.level || 0} data-sentry-element="CardTitleAndValue" data-sentry-source-file="CogStatCalculator.jsx" />
      <FormControl data-sentry-element="FormControl" data-sentry-source-file="CogStatCalculator.jsx">
        <FormLabel id="demo-row-radio-buttons-group-label" data-sentry-element="FormLabel" data-sentry-source-file="CogStatCalculator.jsx">Cog type</FormLabel>
        <RadioGroup row aria-labelledby="demo-row-radio-buttons-group-label" name="row-radio-buttons-group" value={cogType} onChange={handleChange} data-sentry-element="RadioGroup" data-sentry-source-file="CogStatCalculator.jsx">
          <FormControlLabel value={2} control={<Radio />} label="Nooby" data-sentry-element="FormControlLabel" data-sentry-source-file="CogStatCalculator.jsx" />
          <FormControlLabel value={3} control={<Radio />} label="Decent" data-sentry-element="FormControlLabel" data-sentry-source-file="CogStatCalculator.jsx" />
          <FormControlLabel value={4} control={<Radio />} label="Superb" data-sentry-element="FormControlLabel" data-sentry-source-file="CogStatCalculator.jsx" />
          <FormControlLabel value={5} control={<Radio />} label="Ultimate" data-sentry-element="FormControlLabel" data-sentry-source-file="CogStatCalculator.jsx" />
        </RadioGroup>
      </FormControl>

      <Stack direction={'row'} gap={3} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="CogStatCalculator.jsx">
        <Section title={'Construction Value'} main={mainConstructionValue} min={constructionMin} max={constructionMax} data-sentry-element="Section" data-sentry-source-file="CogStatCalculator.jsx" />
        <Section title={'Build rate'} min={Math.floor(constructionMin)} max={Math.floor(constructionMax)} data-sentry-element="Section" data-sentry-source-file="CogStatCalculator.jsx" />
        <Section title={'Flag rate'} min={Math.round(Math.pow(constructionMin, .8))} max={Math.round(Math.pow(constructionMax, .8))} data-sentry-element="Section" data-sentry-source-file="CogStatCalculator.jsx" />
        <Section title={'Exp'} min={Math.max(Math.floor(Math.pow(constructionMin, 0.4) + Math.log10(constructionMin) * 10 - 5), 2)} max={Math.max(Math.floor(Math.pow(constructionMax, 0.4) + Math.log10(constructionMax) * 10 - 5), 2)} data-sentry-element="Section" data-sentry-source-file="CogStatCalculator.jsx" />
      </Stack>
    </Stack>;
};
const Section = ({
  title,
  main,
  min,
  max
}) => {
  return <CardTitleAndValue title={title} data-sentry-element="CardTitleAndValue" data-sentry-component="Section" data-sentry-source-file="CogStatCalculator.jsx">
    <Stack data-sentry-element="Stack" data-sentry-source-file="CogStatCalculator.jsx">
      {main ? <Stack alignItems={'center'} direction={'row'} gap={1}>
        <Typography sx={{
          fontSize: 14
        }} color="text.secondary">main</Typography>
        <Typography>{notateNumber(main, 'MultiplierInfo')}</Typography>
      </Stack> : null}
      <Stack alignItems={'center'} direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="CogStatCalculator.jsx">
        <Typography sx={{
          fontSize: 14
        }} color="text.secondary" data-sentry-element="Typography" data-sentry-source-file="CogStatCalculator.jsx">min</Typography>
        <Typography data-sentry-element="Typography" data-sentry-source-file="CogStatCalculator.jsx">{notateNumber(min, 'MultiplierInfo')}</Typography>
      </Stack>
      <Stack alignItems={'center'} direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="CogStatCalculator.jsx">
        <Typography sx={{
          fontSize: 14
        }} color="text.secondary" data-sentry-element="Typography" data-sentry-source-file="CogStatCalculator.jsx">max</Typography>
        <Typography data-sentry-element="Typography" data-sentry-source-file="CogStatCalculator.jsx">{notateNumber(max, 'MultiplierInfo')}</Typography>
      </Stack>
    </Stack>
  </CardTitleAndValue>;
};
export default CogStatCalculator;